body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  overflow: auto;
}
.logo {
  font-size: 40px;
  color: white;
  text-align: center;
}
.left-sidebar {
  background-color: rgba(0,0,0,.9);
  text-align: center;
  margin: 30px, 0;
}
.search {
  text-align: center;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.form-label {
    color: white;
}
.form-item {
    margin-bottom: 10px;
}
.search-box {
    text-align: center;
}
.title {
    background-color: rgba(0,0,0,.9);
    color: white;
    text-align: center;
    padding-top: 20px;
    font-size: 30pt;
    margin: 30px;
    border-radius: 25px;
}
.searchBox {
    color: white;
    margin: 0 auto;
}
img {
    width: 80%;
    /* height: 100%; */
}
.side-info-holder {
    display: flex;
    flex-flow: column;
    justify-content: space-around;
    align-content: center;
    overflow: auto;
}
.side-info-block {
    justify-content: center;
    overflow: auto;
}
.card-div {
    background-color: rgba(0, 0, 0 , .95);
    text-align: center;
    color: white;
    margin-top: 15px;
    border: thin solid white;
    border-radius: 25px;
    overflow: auto;
}
.card-btn {
    height: 50px;
    width: 50px;
    border: none;
    font-size: 24px;
    cursor: pointer;
    outline: none;
    background: rgba(0, 0, 0 , 0);
}
img {
    border-radius: 15px;
}
.card-title {
    font-size: 18pt;
    font-family: 'Roboto', sans-serif;
}
.card-text {
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 15pt;
}
.fav {
    color: gold;
}
.delete {
    color: red;
}
.news-btn {
    margin-top: 10px;
}

.fav-div {
    height: 200px;
    color: white;
    background-color: rgba( 0, 0, 0 , .9);
    margin: 0, auto;
    text-align: center;
    font-size: 30pt;
}

.gold {
    color: gold;
    cursor: pointer;
}
